import { ref } from "vue";
import { defineStore } from "pinia";

import { UserData } from "@/Type/types";
import UserDetailsData from "@/Json/UserDetails.json";

export const useUserStore = defineStore("user", () => {
  const users = ref<UserData[]>(UserDetailsData.userDetails.users);

  const userDataLocalStorage = localStorage.getItem("userData");

  if (userDataLocalStorage) {
    users.value = JSON.parse(userDataLocalStorage || "[]");
  }

  const updateUserData = (user: UserData) => {
    users.value.push(user);

    localStorage.setItem("userData", JSON.stringify(users.value));
  };

  return { users, updateUserData };
});