<template>
  <div class="header-section w-full py-2.5">
    <div class="title-section">
      <div class="w-12">
        <custom-button
          button-style="shadow-none custom-button"
          :icon="headerData.icons.hamburgerIcon"
          @click="handleToggleButtonClick"
        />
      </div>
      <h1 class="font-medium header-title">{{ headerData.title }}</h1>
    </div>
    <div class="flex items-center justify-end w-full">
      <search-input
        :placeholder="headerData.searchBoxLabel"
        prepend-icon="mdi-magnify"
        class="pl-1.5 search-input search-input--hide"
      />
      <div class="min-w-7 ml-1.25">
        <custom-button
          button-style="shadow-none custom-button"
          :icon="headerData.icons.themeSwitchingIcon"
        />
      </div>
      <div class="min-w-7 ml-1.25">
        <custom-button
          button-style="shadow-none custom-button"
          :icon="headerData.icons.notificationIcon"
        />
      </div>
      <div>
        <options-dropdown
          class="pl-1.25 w-fit user-dropdown"
          :dropdown-content="headerData.userProfile.options"
          :image-url="UserProfileImage"
          should-hide-toggle-arrow
        />
        <div class="relative hidden user-modal" ref="modalContainer">
          <v-icon small class="modal-toggle-icon" @click="toggleModal">
            mdi-dots-vertical
          </v-icon>
          <modal
            :is-modal-active="shouldShowModal"
            class="right-0 w-max modal-container"
          >
            <template #content>
              <div class="p-px">
                <search-input
                  :placeholder="headerData.searchBoxLabel"
                  prepend-icon="mdi-magnify"
                  class="mt-1 search-input search-input--modal"
                />
                <div class="modal-user flex items-center">
                  <img :src="UserProfileImage" class="modal-user--image" />
                  <span class="text-sm">
                    {{ headerData.userProfile.options[0].text }}
                  </span>
                </div>
                <div class="flex items-center modal-logout">
                  <v-icon small class="mr-2">
                    {{ headerData.userProfile.options[1].prependIcon }}
                  </v-icon>
                  <span class="text-3xs leading-3.50">
                    {{ headerData.userProfile.options[1].text }}
                  </span>
                </div>
              </div>
            </template>
          </modal>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { computed, defineEmits, onMounted, ref } from "vue";

import UiData from "@/Json/UIdata.json";

import UserProfileImage from "@/assets/userProfileImage.png";
import CustomButton from "@/components/CustomButton.vue";
import SearchInput from "@/components/SearchInput.vue";
import OptionsDropdown from "@/components/OptionsDropdown.vue";
import Modal from "@/components/Modal.vue";

const emits = defineEmits<{
  (e: "toggle-button-click", value: Event): void;
}>();

const shouldShowModal = ref<boolean>(false);
const modalContainer = ref<HTMLElement | null>(null);

const headerData = computed(() => UiData.topPanel);

const toggleModal = () => {
  shouldShowModal.value = !shouldShowModal.value;
};

const onDocumentClick = (event: Event) => {
  if (!modalContainer.value?.contains(event?.target as HTMLElement))
    shouldShowModal.value = false;
};

const handleToggleButtonClick = (event: Event) => {
  emits("toggle-button-click", event);
};

onMounted(() => {
  document.addEventListener("click", (event: Event) => onDocumentClick(event));
});
</script>

