<template>
  <div>
    <v-form
      v-model="isFormValid"
      ref="form"
      class="grid gap-5"
      :style="formStyle"
    >
      <div v-for="field in props.formFields" :key="field.value">
        <search-input
          :label="field.label"
          :prepend-icon="field.prependIcon"
          :type="field.type"
          :rules="field.rules"
          @on-input-change="onInputChange(field.value, ...arguments)"
        />
      </div>
    </v-form>
    <div class="flex justify-end items-center gap-2 w-100% min-h-11 pt-5">
      <div>
        <custom-button
          :text="props.cancelButtonLabel"
          :variant="ButtonVariants.Outlined"
          class="custom-form-button"
          @click="onCancelClick"
        />
      </div>
      <div class="text-3xs min-h-7">
        <custom-button
          :text="props.submitButtonLabel"
          button-style="min-h-full"
          class="custom-form-button"
          @click="onSubmitClick"
        />
      </div>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { computed, defineEmits, defineProps, ref, withDefaults } from "vue";

import { FormField } from "@/Type/types";
import { ButtonVariants } from "@/constants";

import SearchInput from "@/components/SearchInput.vue";
import CustomButton from "@/components/CustomButton.vue";

interface Props {
  formFields: FormField[];
  submitButtonLabel?: string;
  cancelButtonLabel?: string;
  columnCount?: number;
}

type FormData = Record<string, string> | null;

const props = withDefaults(defineProps<Props>(), {
  submitButtonLabel: "Save",
  cancelButtonLabel: "Cancel",
  columnCount: 1,
});

const emits = defineEmits<{
  (e: "cancel", value: Event): void;
  (e: "submit", value: FormData): void;
}>();

const form = ref<HTMLFormElement | null>(null);
const isFormValid = ref<boolean>(false);
const formData = ref<FormData>(null);

const formStyle = computed(() => {
  return {
    "grid-template-columns": `repeat(${props.columnCount}, minmax(0, 1fr))`,
  };
});

const onSubmitClick = () => {
  form.value?.validate();

  if (isFormValid.value) {
    emits("submit", formData.value);
  }
};

const onCancelClick = (event: Event) => {
  emits("cancel", event);
};

const onInputChange = (fieldValue: string, inputValue: string) => {
  formData[fieldValue] = inputValue;
};
</script>

<style lang="scss" scoped>
.custom-form-button {
  font-size: 13px !important;
}
</style>