<template>
  <div class="multi-select-container shadow-multi-select" @click.stop>
    <div class="pt-3 px-2">
      <div class="selection-panel">
        <v-checkbox
          v-model="isSelectedAll"
          :label="props.selectAllText"
          :indeterminate="isIndeterminate"
          class="select-all"
          color="#03a9f4"
          hide-details
          @change="onSelectAllClick"
        />
        <v-checkbox
          v-for="item in props.options"
          :key="item"
          v-model="selectedItems"
          :label="item"
          ripple
          color="#03a9f4"
          :value="item"
          hide-details
          class="select-item"
        />
      </div>
    </div>
    <div class="footer">
      <button class="footer__button" @click="onSubmitClick">
        {{ props.submitButtonLabel }}
      </button>
      <button class="footer__button" @click="onCancelClick">
        {{ props.cancelButtonLabel }}
      </button>
    </div>
  </div>
</template>

<script setup lang="ts">
import {
  computed,
  defineEmits,
  defineProps,
  ref,
  watch,
  withDefaults,
} from "vue";

interface Props {
  options: string[];
  cancelButtonLabel?: string;
  submitButtonLabel?: string;
  selectAllText?: string;
}

const props = withDefaults(defineProps<Props>(), {
  cancelButtonLabel: "",
  submitButtonLabel: "",
  selectAllText: "Select All",
});

const emit = defineEmits<{
  (e: "on-submit-click", value: string[]): void;
  (e: "on-cancel-click", value: Event): void;
}>();

const selectedItems = ref<string[]>([]);
const isSelectedAll = ref<boolean>(false);

const selectItemsLength = props.options.length;

const isIndeterminate = computed(
  () => !!selectedItems.value.length && selectedItems.value.length < selectItemsLength
);

const onSelectAllClick = () => {
  selectedItems.value = isSelectedAll.value ? [...props.options] : [];
};

const onSubmitClick = () => {
  emit("on-submit-click", selectedItems.value);
};

const onCancelClick = (event: Event) => {
  emit("on-cancel-click", event);
};

watch(selectedItems, (newValue) => {
  switch (newValue.length) {
    case selectItemsLength:
      isSelectedAll.value = true;
      break;
    default:
      isSelectedAll.value = false;
  }
});
</script>

