<template>
  <div
    class="w-screen h-screen flex justify-center bg-black-6 absolute left-0 add-user-wrapper"
  >
    <div class="add-user-container">
      <div
        class="add-user-container__title border-b border-b-gray-200 text-left flex items-center"
      >
        <h1 class="text-base/5 font-medium px-4">
          {{ NEW_CONTACT_TITLE }}
        </h1>
      </div>
      <div class="add-user-container__contents">
        <div class="drop-box" @click="onImageInputClick">
          <input type="file" class="hidden" ref="imageInputElement" />
          <span class="drop-box__text">{{ IMAGE_INPUT_BOX_TEXT }}</span>
        </div>
        <div class="pt-2.5">
          <form-component
            :form-fields="formFields"
            :column-count="2"
            @cancel="onFormCancel"
            @submit="onFormSubmit"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { defineEmits, onMounted, ref } from "vue";

import {
  ALPHABETS_REGEX_PATTERN,
  ADDRESS_REGEX_PATTERN,
  EMAIL_REGEX_PATTERN,
  COMPANY_NAME_REGEX_PATTERN,
} from "@/constants";
import { NewUserDetails, UserDetails } from "@/Type/types";

import FormComponent from "@/components/FormComponent.vue";

const emit = defineEmits<{
  (e: "cancel"): void;
  (e: "submit", value: NewUserDetails): void;
}>();

const rules = {
  required: (value: string | number) => !!value,
  email: (value: string) => {
    return EMAIL_REGEX_PATTERN.test(value);
  },
  length: (value: string) => value.length >= 3 && value.length <= 20,
  name: (value: string) => ALPHABETS_REGEX_PATTERN.test(value),
  address: (value: string) => ADDRESS_REGEX_PATTERN.test(value),
  company: (value: string) => COMPANY_NAME_REGEX_PATTERN.test(value),
};

const imageInputElement = ref<HTMLInputElement | null>(null);
const imageURL = ref<string>("");

const NEW_CONTACT_TITLE = "New Contact";
const IMAGE_INPUT_BOX_TEXT = "Click the area to select a photo from a folder";

const formFields = [
  {
    label: "First Name",
    type: "text",
    rules: [rules.length, rules.required, rules.name],
    value: "firstName",
  },
  {
    label: "Last Name",
    type: "text",
    rules: [rules.name],
    value: "lastName",
  },
  {
    label: "Company",
    type: "text",
    rules: [rules.length, rules.required, rules.company],
    value: "company",
  },
  {
    label: "Position",
    type: "text",
    rules: [rules.length, rules.required, rules.name],
    value: "position",
  },
  {
    label: "Assigned to",
    type: "text",
    rules: [rules.length, rules.required, rules.name],
    value: "assignedTo",
  },
  {
    label: "Phone",
    type: "number",
    prependIcon: "mdi-phone",
    rules: [rules.required],
    value: "phone",
  },
  {
    label: "Email",
    type: "text",
    prependIcon: "mdi-email",
    rules: [rules.length, rules.required, rules.email],
    value: "email",
  },
  {
    label: "Address",
    type: "text",
    prependIcon: "mdi-home",
    rules: [rules.length, rules.required, rules.address],
    value: "address",
  },
];

const onImageInputClick = () => {
  imageInputElement.value?.click();
};

const onImageInputChange = () => {
  imageURL.value = imageInputElement.value?.files[0]
    ? URL.createObjectURL(imageInputElement.value?.files[0])
    : "";
};

const onFormCancel = () => {
  emit("cancel");
};

const onFormSubmit = (formData: UserDetails) => {
  const { firstName, lastName, ...otherValues } = formData;

  const userData = {
    ...otherValues,
    name: `${firstName} ${lastName}`,
    imageURL: imageURL.value,
  };

  emit("submit", userData);
};

onMounted(() => {
  imageInputElement.value?.addEventListener("change", onImageInputChange);
});
</script>

