          >
            <template #content>
              <div class="pt-2.5 px-2.75 pb-2.25 hidden add-contact--modal">
                <custom-button
                  :text="containerData.addContactButton.title"
                  :icon="containerData.addContactButton.icon"
                  button-style="custom-button--modal"
                  @click="onAddContactClick"
                />
              </div>
              <div
                class="hidden justify-start items-center px-2.75 h-9 data-refresh-button--modal"
              >
                <v-icon small class="mr-1 modal-icons">
                  {{ containerData.refreshDataButton.icon }}
                </v-icon>
                <span class="text-3xs leading-3.50 tracking-extra-wide">
                  {{ containerData.refreshDataButton.title }}
                </span>
              </div>
              <span class="hidden separator-modal" />
              <div
                v-for="(option, index) in containerData.exportOptions.options"
                :key="option.text"
                :class="[
                  'text-left pt-2.5 px-2.75 pb-2.25 items-center hidden export-data--modal',
                  {
                    'mt-1': index === 0,
                    'mb-1':
                      index === containerData.exportOptions.options.length - 1,
                  },
                ]"
              >
                <v-icon small class="mr-2">{{ option.prependIcon }}</v-icon>
                <span class="text-3xs leading-3.50">{{ option.text }}</span>
              </div>
              <span class="hidden separator-modal" />
              <div
