                  {{ containerData.columnChooserButton.icon }}
                </v-icon>
                <span class="text-3xs leading-3.50 tracking-extra-wide">
                  {{ containerData.columnChooserButton.title }}
                </span>
              </div>
              <div
                class="mb-1 pt-2.5 px-2.75 pb-2.25 hidden contact-search--modal"
              >
                <search-input
                  :placeholder="containerData.searchBoxLabel"
                  prepend-icon="mdi-magnify"
