          <span class="mr-0.75">{{ header.text }}</span>
          <v-icon
            small
            class="header-filter-icon"
            @click.stop="() => onTableFilterIconClick(headerData.value)"
          >
            mdi-filter-variant
          </v-icon>
          <multi-select
            v-show="selectedTableHeader === headerData.value"
            :options="tableColumnData(headerData.value)"
            :submit-button-label="MULTI_SELECT_SUBMIT_LABEL"
            :cancel-button-label="MULTI_SELECT_CANCEL_LABEL"
            class="multi-select"
            @on-submit-click="onMultiSelectSubmit"
            @on-cancel-click="onMultiSelectCancel"
          />
        </div>
      </template>
      <template v-slot:item.name="{ item }">
        <td>
          <div class="max-h-8 flex flex-col">
            <span class="leading-4 inline-block">{{ item.name }}</span>
            <span class="block text-xs text-black-6">
              {{ item.position }}
            </span>
          </div>
        </td>
      </template>
      <template v-slot:item.status="{ item }">
        <td class="user-status-field">
          <icon-text-field
            :class="`${UserStatusClass[item.status]}`"
            :text="item.status"
            text-style="text-3xs leading-3.50 text-inherit"
            prepend-icon="mdi-circle"
            prepend-icon-style="mr-1.25 user-status-prepend-icon"
          />
        </td>
      </template>
      <template v-slot:expanded-item="{ headers: columnHeaders, item }">
        <td :colspan="columnHeaders.length" class="expanded-data">
          <ul class="expanded-data__list">
            <li v-for="data in props.tableExpandData" :key="data">
              <span class="expand-item-title capitalize">{{ data }}</span>
              <div
                v-if="data === TABLE_DATA_USER_STATUS_KEY"
                class="h-6.5 flex justify-center items-center"
              >
                <icon-text-field
                  :class="`${UserStatusClass[item.status]}`"
                  :text="item.status"
                  text-style="text-3xs leading-3.50 text-inherit"
                  prepend-icon="mdi-circle"
                  prepend-icon-style="mr-1.25 user-status-prepend-icon"
                />
              </div>
              <span v-else class="expand-item-data block">
                {{ item[data] }}
              </span>
            </li>
          </ul>
        </td>
      </template>
    </table-component>
    <add-user-card
      v-if="shouldShowAddUserCard"
      @cancel="onFormCancelClick"
      @submit="onSubmit"
    />
  </div>
</template>

<script setup lang="ts">
import { computed, defineEmits, defineProps, ref, withDefaults } from "vue";

import { useUserStore } from "@/store/userStore";
import { NewUserDetails, TableHeader, UserData } from "@/Type/types";
import { UserStatusClass } from "@/constants";

import UIdata from "@/Json/UIdata.json";
import TableComponent from "@/components/TableComponent.vue";
import SearchInput from "@/components/SearchInput.vue";
import OptionsDropdown from "@/components/OptionsDropdown.vue";
import CustomButton from "@/components/CustomButton.vue";
import IconTextField from "@/components/IconTextField.vue";
import AddUserCard from "@/containers/AddUserCard.vue";
import MultiSelect from "@/components/MultiSelect.vue";
import Modal from "@/components/Modal.vue";

interface Props {
  tableData: UserData[];
  tableHeader: TableHeader[];
  tableExpandData?: string[];
}

const props = withDefaults(defineProps<Props>(), {
  tableExpandData: () => [],
});

const emits = defineEmits<{
  (e: "row-click", value: UserData): void;
  (e: "status-option-click", value: string): void;
}>();

const userStore = useUserStore();
const searchText = ref<string>("");
const shouldShowAddUserCard = ref<boolean>(false);
const selectedTableHeader = ref<string>("");
const selectedUserData = ref<string[]>([]);
const shouldShowModal = ref<boolean>(false);

const TABLE_DATA_USER_STATUS_KEY = "status";
const MULTI_SELECT_SUBMIT_LABEL = "Submit";
const MULTI_SELECT_CANCEL_LABEL = "Cancel";

const containerData = computed(() => UIdata.contactListPage);

const tableHeaderWithText = computed(() => {
  return props.tableHeader.filter((header: TableHeader) => !!header.text);
});

const users = computed(() => {
  if (selectedUserData.value.length) {
    const isDataSelected = (data: string) =>
      selectedUserData.value.includes(data);

    const data = props.tableData.filter((tableData: UserData) =>
      Object.values(tableData).some(isDataSelected)
    );

    return data;
  }

  return props.tableData;
});

const onSubmit = (newUserData: NewUserDetails) => {
  shouldShowAddUserCard.value = false;

  /* As form data doesn't have enough details about user,
   * rest of the data has been taken from another random user at index "0" */
  const { status, userOpportunities, userActivities, imageURL } =
    props.tableData[0];
  const profileImage = newUserData.imageURL || imageURL;

  const userData = {
    ...newUserData,
    status,
    userActivities,
    userOpportunities,
    imageURL: profileImage,
  };

  userStore.updateUserData(userData);
};

const tableColumnData = (userDataField: string) => {
  const columnData = users.value.map(
    (userData: UserData) => userData[userDataField]
  );

  return [...new Set(columnData)];
};

const handleTableRowClick = (TableRowData: UserData) => {
  emits("row-click", TableRowData);
};

const onDropdownItemClick = (userStatus: string) => {
  const selectedUserStatus =
    containerData.value.categoryOptions.options[0].value === userStatus
      ? ""
      : userStatus;

  emits("status-option-click", selectedUserStatus);
};

const onAddContactClick = () => {
  shouldShowAddUserCard.value = !shouldShowAddUserCard.value;
};

const onUserSearch = (inputValue: string) => {
  searchText.value = inputValue;
};

const onTableFilterIconClick = (selectedHeader: string) => {
  selectedTableHeader.value = selectedHeader;
};

const onMultiSelectCancel = () => {
  selectedTableHeader.value = "";
};

const onMultiSelectSubmit = (selectedItems: string[]) => {
  selectedUserData.value = [...selectedUserData.value, ...selectedItems];
  selectedTableHeader.value = "";
};

const onFormCancelClick = () => {
  shouldShowAddUserCard.value = false;
};

const toggleModal = () => {
  shouldShowModal.value = !shouldShowModal.value;
};
</script>

